<script>
import { Button } from "iview";
import {
  getMoneyIndustryList,
  getMoneyQuality,
  getProjectNewest,
  getProjectSuperior
} from "../../api/public";
import { imgUrl } from "../../utils";
export default {
  components: {
    [Button.name]: Button
  },
  data() {
    return {
      imgUrl: imgUrl,
      moneyIndustryList: [], //行业精选资金
      moneyQuality: [], //精选资金
      projectNewest: [], //最新项目
      projectSuperior: [] //优质项目
    };
  },
  methods: {
    //获取行业精选资金
    getMoneyIndustryList() {
      getMoneyIndustryList({ size: 6 }).then(res => {
        this.moneyIndustryList = res.data.data;
      });
    },
    //获取精选资金
    getMoneyQuality() {
      getMoneyQuality({ size: 6 }).then(res => {
        this.moneyQuality = res.data.data;
      });
    },
    //获取最新项目
    getProjectNewest() {
      getProjectNewest({ size: 6 }).then(res => {
        this.projectNewest = res.data.data.rows;
      });
    },
    //获取优质项目
    getProjectSuperior() {
      getProjectSuperior({ size: 6 }).then(res => {
        this.projectSuperior = res.data.data;
      });
    }
  },
  created() {
    this.getMoneyIndustryList();
    this.getMoneyQuality();
    this.getProjectNewest();
    this.getProjectSuperior();
  }
};
</script>
